<template>
  <nav class="filter-nav">
    <button @click="update_filter('all')" :class="{active: current==='all'}">View All</button>
    <button @click="update_filter('completed')" :class="{active: current==='completed'}">Completed</button>
    <button @click="update_filter('on')" :class="{active: current==='on'}">On Going</button>

  </nav>
</template>

<script>
export default {
  name: "FilterNav",
  props: ['current'],
  methods: {
    update_filter(by) {
      this.$emit('filter-change', by)
    }
  }
}
</script>

<style>
.filter-nav button {
  background: none;
  border: none;
  color: #bbb;
  outline: none;
  font-size: 12px;
  text-transform: uppercase;
  margin-right: 10px;
  letter-spacing: 1px;
  font-weight: bold;
  cursor: pointer;
}
.filter-nav button.active{
  color: #555;
}
</style>